	module.exports = {"data":{"site":{"siteMetadata":{"title":"Fog Up Some Comics","author":"Austin Lanari"}},"markdownRemark":{"id":"/home/austin/newFusc/FuSC/src/pages/2017-08-08-Black-Blizzard/index.md absPath of file >>> MarkdownRemark","html":"<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"undefined\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; z-index: -1; display: block; \"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 144.44444444444443%;position: relative; width: 100%; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Black Blizzard Parade\"\n        title=\"\"\n        src=\"/static/78937f7a7641b54e649cc9af9361d79a-f8fb9.jpg\"\n        srcset=\"/static/78937f7a7641b54e649cc9af9361d79a-e8976.jpg 148w,\n/static/78937f7a7641b54e649cc9af9361d79a-63df2.jpg 295w,\n/static/78937f7a7641b54e649cc9af9361d79a-f8fb9.jpg 590w,\n/static/78937f7a7641b54e649cc9af9361d79a-85e3d.jpg 885w,\n/static/78937f7a7641b54e649cc9af9361d79a-a5ccc.jpg 900w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>Tatsumi’s thoughts about <em>Black Blizzard</em> as a work are pretty telling.</p>\n<blockquote>\n<p>[B]ringing this work out as a book now is like exposing something shameful and private from my past that I’d rather keep hidden from sight.</p>\n</blockquote>\n<p>In the quoted interview (with Adrian Tomine, featured in the back of D&#x26;Q’s adaptation), Tomine quickly and gently (and correctly) rebukes Tatsumi for being too hard on his work.</p>\n<p>But the pace at which Tatsumi put this book together (20 days!!!) often shows. <em>Black Blizzard</em> is straight out of a noir movie that sounds like it was translated from English into Japanese and then back again. Of course, this book was never meant to sit on anybody’s shelf: it was a rental manga at a time when the industry was booming and Tatsumi was cranking literally dozens of books out. That’s not to say the work is objectively at a lower standard; yet, it is not as if we can divorce ourselves from the expectations we have of books that we pay for and re-read. Our disposition towards books is entirely different than someone’s disposition would have been towards a <em>rental</em> book in 1950’s Japan.</p>\n<p>That said, the book didn’t even really do well with readers back then. The main demographic that seemed taken by Tatsumi’s work in this case were his peers, and I can understand why. The book is a pretty early example of a creator who wasn’t named “Tezuka” adapting essential elements of cinema in interesting ways. Still, the impact of these innovations was often limited to individual sequences, and thus benefits someone with an eye towards dissecting the work, rather than someone who is just interested in the story itself. Tatsumi wasn’t yet comfortable enough with the tricks of the trade to get them to work consistently throughout, and the result is something charming and curious in spite of overall execution.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"undefined\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; z-index: -1; display: block; \"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 102.23162754905732%;position: relative; width: 100%; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Black Blizzard Piano\"\n        title=\"\"\n        src=\"/static/2a959f7844320584f23f7c724baaa6e2-f8fb9.jpg\"\n        srcset=\"/static/2a959f7844320584f23f7c724baaa6e2-e8976.jpg 148w,\n/static/2a959f7844320584f23f7c724baaa6e2-63df2.jpg 295w,\n/static/2a959f7844320584f23f7c724baaa6e2-f8fb9.jpg 590w,\n/static/2a959f7844320584f23f7c724baaa6e2-85e3d.jpg 885w,\n/static/2a959f7844320584f23f7c724baaa6e2-d1924.jpg 1180w,\n/static/2a959f7844320584f23f7c724baaa6e2-9452e.jpg 1770w,\n/static/2a959f7844320584f23f7c724baaa6e2-2a068.jpg 2599w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>Notice the focus on the main character’s expression here. Notice just how much this panel is <em>about</em> what he is feeling, and how it is represented in various ways: his expression, the sweat drops coming off of him, the frantic lettering and the kinds of sounds it entails if you imagine somebody at a piano, the fact that the frantic lettering penetrates the sanctity of the panel, and that pair of eight notes that is dangerously close to the panel above.  <em>Black Blizzard</em> was beloved by Tatsumi’s colleagues because it represented a sharp shift in focus from mere storytelling or draftstmanship towards the cinematic and the outright dramatic in exactly this manner.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"undefined\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; z-index: -1; display: block; \"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 102.23953261927944%;position: relative; width: 100%; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Black Blizzard Smoke\"\n        title=\"\"\n        src=\"/static/0ea714314eee6acdd244efba7a0abace-f8fb9.jpg\"\n        srcset=\"/static/0ea714314eee6acdd244efba7a0abace-e8976.jpg 148w,\n/static/0ea714314eee6acdd244efba7a0abace-63df2.jpg 295w,\n/static/0ea714314eee6acdd244efba7a0abace-f8fb9.jpg 590w,\n/static/0ea714314eee6acdd244efba7a0abace-85e3d.jpg 885w,\n/static/0ea714314eee6acdd244efba7a0abace-e5aa2.jpg 1027w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>Admittedly, my favorite sequence in the book is the top image of this piece. The main character meets the love of his life because he dropped his music and she brought it to him… in the <em>middle of a fucking parade!</em> The whole thing is just so goofy, despite how central it is to the story. Something about the final image of the woman on the float looking back gave me <em>very</em> strong Satoshi Kon vibes. It’s an anachronism, I know, but this kind of goofy earnestness is a surprisingly poignant vehicle in a lot of more modern Japanese media.</p>\n<p>My unironic favorite part is the above sequence, where the main character disappears into the fog of the night with his arresting officer and then fades in to a similar cloudy scene, where the smoke is being caused by the nearby fire as he recounts his tale to a fellow escaped convict. The cinematic and the dramatic are especially evident in this scene: again, character expressions have entire panels dedicated to them. In addition, I can’t think of something much more cinema-specific than trenchcoated figures walking off into a foggy distance. The other star are the diagonal lines adding texture to the outdoor scene. That kind of explicit ambient work was yet another way Tatsumi infused particular shots with drama.</p>\n<p>I can’t get over the second to that third panel (note: in case you haven’t figured it out, this manga is flipped and reads like Western comics, left to right). It is a sudden transition but the focus on expression and the environmental elements carry the steady beat of the first two panels into a crescendo that is simultaneously loud and serene. It is the culmination of everything up to that point in the book, and yet when you see what all of that adds up to for the main character—when you are faced with <em>only</em> what it means to him—a quiet penetrates the page, and the exposition in the next panel feels like it’s being delivered not as plot details but as the lived experience of a man whose life has been shattered.</p>\n<p>Although this sequence works, and beautifully so, its power is in its simplicity, and too many sequences delivered as such would make the book feel like a storyboard. Ironically, <em>Black Blizzard</em> <strong>doesn’t</strong> read like storyboards on the whole. While the narrative is well-paced, Tatsumi often seems unsure of how to vary the beats as they are presented visually. At one point, the last panel before a page turn is the exact same as the first panel after the page turn, and it’s jarringly unpleasant.  <em>Black Blizzard</em> is best understood as a mixed bag of sore thumbs and innovative flourishes held together by a competent storyteller paying his dues.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"undefined\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; z-index: -1; display: block; \"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 145.2513966480447%;position: relative; width: 100%; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Black Blizzard Lights\"\n        title=\"\"\n        src=\"/static/64154eb1a3d4f75946cf1cce75f82672-f8fb9.jpg\"\n        srcset=\"/static/64154eb1a3d4f75946cf1cce75f82672-e8976.jpg 148w,\n/static/64154eb1a3d4f75946cf1cce75f82672-63df2.jpg 295w,\n/static/64154eb1a3d4f75946cf1cce75f82672-f8fb9.jpg 590w,\n/static/64154eb1a3d4f75946cf1cce75f82672-85e3d.jpg 885w,\n/static/64154eb1a3d4f75946cf1cce75f82672-10e26.jpg 895w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>","fields":{"slug":"/2017-08-08-Black-Blizzard/"},"frontmatter":{"title":"Tatsumi's 'Black Blizzard'","date":"August 08, 2017","indexImage":{"childImageSharp":{"resize":{"src":"/static/2a959f7844320584f23f7c724baaa6e2-30c8e.jpg","originalName":"tatsumi_title.jpg"}}},"layout":"post","itemTitle":"Black Blizzard","itemAuthor":"Yoshihiro Tatsumi","itemType":"Book","itemPublisher":"Drawn & Quarterly","description":"Tatsumi's most notable early work says a lot about the form and how we interact with it."}}},"pathContext":{"slug":"/2017-08-08-Black-Blizzard/"}}

